import React, { CSSProperties, TextareaHTMLAttributes } from "react";

import { InputValidation, StyledRequirementProps } from "../Input";
import { Margins } from "../styleguide";


type TextAreaAttributes = TextareaHTMLAttributes<HTMLTextAreaElement>;

export interface TextAreaProps extends
	StyledTextAreaProps, StyledRequirementProps, Omit<TextAreaAttributes, "onChange"> {
	label?: string
	value?: string | number
	requirements?: string[]
	topButtonLinkText?: string
	topButtonLinkAction?: () => void
	onChange?: (event: React.ChangeEvent<HTMLTextAreaElement> ) => void
	dataQA: string
	id: string
}

export interface StyledTextAreaProps extends Margins, Pick<TextAreaBase, "disabled"> {
	validation?: InputValidation
	$resize?: CSSProperties["resize"]
}
export interface TextAreaBase extends TextAreaAttributes {
	dataQA: string
}
